.Final-container {
  position: relative;
  width: 100%;
  height: 11000px;
  & .Final-image-container {
    box-shadow: inset 0px 250px 0px var(--dark);
    width: 100%;
    height: 100vh;
    position: sticky;
    top: 0;
    left: 0;
    overflow: hidden;
    & .Final-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 10px;
    }
  }
}
